import misEnum from '../../common/mis-enum.js';

function getDate(addDay) {
	const date = new Date();
	if (addDay > 0) {
		date.setDate(date.getDate() + addDay);
	}
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();
	month = month > 9 ? month : '0' + month;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}

export default {
	data() {
		return {
			nowDate: getDate(),
			info: {}
		}
	},
	onLoad(option) {
		this.info = JSON.parse(decodeURIComponent(option.para));
	},
	methods: {
		cancleBook: function(){
			uni.showModal({
				title: '提示',
				content: '确定取消预定？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if(res.cancel) return;
					uni.showLoading({
						title: '提交中',
						mask: false
					});
					global.$http.post('/dining/lead/cancel', {
						params: {
							id: this.info.id
						},
					}).then(res => {
						if (res.status === "0") {
							uni.showToast({
								title: "取消成功",
								icon: 'none'
							});
							uni.navigateBack();
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						}
					}).catch(err => {
						uni.showToast({
							title: err.message,
							icon: 'none'
						});
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getStatusStr(status) {
			return misEnum.LeaderBookFoodEnumMap.get(status);
		},
		toSaveLeaderBook: function(e){
			uni.navigateTo({
				url: '../work/saveLeaderBook?para=' + encodeURIComponent(JSON.stringify(this.info))
			});
		}
	}
}
